}

.bar-1{
    fill: var(--ac-white);
    -webkit-animation: scaling 2000ms ease-in-out;
            animation: scaling 2000ms ease-in-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}


.bar-2{
    fill: var(--ac-white);
    -webkit-animation: scaling 2000ms ease-in-out;
            animation: scaling 2000ms ease-in-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-delay: 250ms;
            animation-delay: 250ms;
}

.bar-3{
    fill: var(--ac-white);
    -webkit-animation: scaling 2000ms ease-in-out;
            animation: scaling 2000ms ease-in-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
}

.rotate-1{
    -webkit-animation: rotate-1 400ms ease-in-out;
            animation: rotate-1 400ms ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

}

.rotate-2{
    -webkit-animation: rotate-2 400ms ease-in-out;
            animation: rotate-2 400ms ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms
}

.rotate-3{
    -webkit-animation: rotate-3 400ms ease-in-out;
            animation: rotate-3 400ms ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms
}


