@import url(./variables.css);



.about h1 {
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    text-orientation: sideways;
    float: right;
    
    font-size: 3.7rem;
    margin: auto;
    margin-right: 0;
    
    margin-left: 0.3rem;
    text-align: left;
}

.follow{
    font-size: 2rem;
    margin: auto;
}


    

.bar {
    background-color: var(--ac-gray);
    padding: 1rem;
    display: inline-block;
    padding-right: 0;
    z-index: 10;
    position: fixed;
    /* margin-left: 25%; */
    width: 300px;
    -webkit-filter: drop-shadow(8px 8px 5px var(--ac-primary));
            filter: drop-shadow(8px 8px 5px var(--ac-primary));
    
}

.about-content{
    text-justify: distribute;
    text-align: right;
    font-size: 0.9rem;
}



@-webkit-keyframes glow-icon {
    0% {
        opacity: 1;
    }
    25% {
        -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
                filter: drop-shadow(0px 0px 10px var(--ac-white));
        
    }
    50% {
        opacity: 1;
        fill: var(--ac-white);
    }
    75% {
        opacity: 1;
    }
    100%{
        opacity: 1;
    }

}



@keyframes glow-icon {
    0% {
        opacity: 1;
    }
    25% {
        -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
                filter: drop-shadow(0px 0px 10px var(--ac-white));
        
    }
    50% {
        opacity: 1;
        fill: var(--ac-white);
    }
    75% {
        opacity: 1;
    }
    100%{
        opacity: 1;
    }

}


.social-icons {
    -webkit-animation: glow-icon 5000ms ease-in infinite;
            animation: glow-icon 5000ms ease-in infinite;
    fill-opacity: 0; 
    stroke-width: 2; 
    stroke: var(--ac-white);
    width: 80px;
    display: inline-block;
}
.facebook {
    fill: var(--ac-white);
    stroke: var(--ac-primary);
    
}
.linkedin {
    fill:var(--ac-white);
    stroke: var(--ac-primary);
    

}
.instagram {
    fill: var(--ac-white);
    fill-opacity: 1;
}
.youtube {
    fill:var(--ac-primary);
    stroke: var(--ac-primary);
    
}
.googleplus {
    fill:var(--ac-white);
}
.github {
    fill:var(--ac-white);
}

.social-icons:hover {
    -webkit-transition: fill-opacity 1000ms, -webkit-filter 1500ms;
    transition: fill-opacity 1000ms, -webkit-filter 1500ms;
    -o-transition: fill-opacity 1000ms, filter 1500ms;
    transition: fill-opacity 1000ms, filter 1500ms;
    transition: fill-opacity 1000ms, filter 1500ms, -webkit-filter 1500ms;
    -webkit-animation: none;
            animation: none;
    -webkit-filter: drop-shadow(0px 0px 10px var(--ac-primary));
            filter: drop-shadow(0px 0px 10px var(--ac-primary));
    fill-opacity: 1;
    fill: var(--ac-primary);
}

.youtube:hover {
    -webkit-transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    -o-transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms, -webkit-filter 1500ms;
    -webkit-animation: none;
            animation: none;
    -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
            filter: drop-shadow(0px 0px 10px var(--ac-white));
    fill-opacity: 1;
    fill: var(--ac-primary);
}




@media only screen and (max-width: 1250px) {
    .social-icons {
        width: 40px;
        -webkit-animation: none;
                animation: none;
    }

    .about h1 {
        -webkit-writing-mode: horizontal-tb;
            -ms-writing-mode: lr-tb;
                writing-mode: horizontal-tb;
        text-orientation: upright;
        text-align: center;
        float: none;
    }
    .bar {
        margin:auto;
        padding-right: 1em;
        position: relative;
        display: block;
        width: 87%;
        line-height: 5em;
        margin-top: 10px;
        -webkit-filter: drop-shadow(4px 4px 3px var(--ac-primary));
                filter: drop-shadow(4px 4px 3px var(--ac-primary));
    
    }
    .about-content {
        line-height: 1.75em;
        margin: auto;

        text-align: center;
    }
}


